* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --black: #111;
  --black_1: #202024;
  --black_2: #00000040;
  --black_3: #0009;
  --grey: #b4afaf;
  --lightgrey: #e0e0e0;
  --violet: #4f2ee8;
  --white: #fff;
  --dark-violet: #4f2ee866;
  --font-size-extrabig: 48px;
  --font-size-big: 32px;
  --font-size-normal: 22px;
  --font-size-normal_1: 18px;
  --font-size-small: 16px;
  --fint-size-small_1: 14px;
  --font-size-small_2: 12px;
}

img {
  border-radius: 8px;
}

.visible-flex {
  display: flex;
}

.visible-block {
  display: block;
}

.novisible, .is_hidden {
  display: none;
}

.icon_light, .lock-icon {
  display: block;
}

.modal_mobile, .lock_icon-dark, .modal_input-icon-pass-white, .modal_input-icon-pass-white, .img_dark-close, .hamburger_open-svg-white, .img_dark-close, .mobile-nav {
  display: none;
}

.dark-mode {
  color: #fff;
  background-color: #333;
}

.logo {
  height: 28px;
}

.logo_dark {
  display: none;
}

.light_mode {
  width: 40px;
  height: 24px;
}

.dark_mode {
  width: 40px;
  height: 24px;
  display: none;
}

.btn_log-out-desktop, .btn_log-out-mobile, .hamburger_open, .hamburger_close {
  display: none;
}

.header_wrapper {
  color: inherit;
  background-color: inherit;
  border: 1.5px solid #000;
  border-radius: 0 0 8px 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 72px;
  display: flex;
  position: relative;
}

.logo {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  left: 2%;
}

.home_shopping_list {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  margin-left: 76px;
  display: flex;
  position: absolute;
  left: 20%;
}

.shoping-list_text {
  z-index: 5;
  color: inherit;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.shopping_link {
  color: inherit;
  background-color: inherit;
  z-index: 5;
  justify-content: center;
  align-items: center;
  margin-left: 40px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.shopping_link:hover:after {
  content: "";
  z-index: -1;
  color: #000;
  background-color: #eac645;
  border-radius: 150px;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 45px;
  display: flex;
  position: absolute;
}

.home_link {
  background-color: inherit;
  color: inherit;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.home_text {
  color: inherit;
  z-index: 2;
  justify-content: center;
  align-items: center;
  display: flex;
}

.home_link:hover:after {
  content: "";
  z-index: 1;
  background-color: #eac645;
  border-radius: 150px;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 45px;
  display: flex;
  position: absolute;
}

.lock-icon {
  fill: #fff;
  margin-left: 5%;
}

.menu_flex {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  right: 10px;
}

.button_login {
  background-color: inherit;
}

.switcher_button {
  justify-content: center;
  align-items: center;
  margin-right: 15px;
  display: flex;
}

#themeSwitch {
  opacity: 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.stefan_btn {
  cursor: pointer;
  background-color: #4f2ee8;
  border: 1px solid #000;
  border-radius: 150px;
  gap: 40px;
  width: 184px;
  height: 48px;
  display: none;
  position: relative;
}

.stefan {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  left: 5px;
}

.stefan_name {
  color: #fff;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  display: flex;
  position: absolute;
  top: 12px;
  left: 60px;
}

.arrow_stefan {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 9px;
  right: 5px;
}

.btn_log-in {
  cursor: pointer;
  background-color: inherit;
  border: none;
}

.backdrop {
  z-index: 15;
  background-color: #4f2ee8;
  background-image: radial-gradient(circle at 0 0, #786be7 1%, #4f2ee8 50%);
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal_signup {
  z-index: 55;
  z-index: 55;
  background-color: #f6f6f6;
  border: 2px solid #000;
  border-radius: 18px;
  width: 100%;
  max-width: 579px;
  margin: 0 auto;
  position: absolute;
  inset: 50px 0;
}

.modal_signup-form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin-top: 15%;
  display: flex;
  position: relative;
}

.modal_signup-field-name, .modal_signup-field-email, .modal_signup-field-pass {
  background-color: inherit;
  border-radius: 150px;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 499px;
  height: 64px;
  display: flex;
  position: relative;
}

::placeholder {
  color: inherit;
}

.modal_input-name, .modal_input-mail, .modal_input-pass {
  color: #000;
  background-color: #f6f6f6;
  border: none;
  justify-content: center;
  align-items: center;
  width: 60%;
  max-width: 286px;
  height: 24px;
  display: flex;
  position: absolute;
  top: 15px;
  left: 20px;
}

.modal_singup_close {
  cursor: pointer;
  background-color: #f6f6f6;
  border: none;
  position: absolute;
  top: 15px;
  right: 20px;
}

.modal_btn {
  color: #f6f6f6;
  cursor: pointer;
  text-align: center;
  background-color: #000;
  border-radius: 150px;
  width: 80%;
  max-width: 499px;
  height: 64px;
}

.modal_input-icon-mail {
  position: absolute;
  top: 15px;
  right: 40px;
}

.modal_input-icon-mail-white {
  display: none;
  position: absolute;
  top: 15px;
  right: 40px;
}

.modal_input-icon-pass {
  position: absolute;
  top: 15px;
  right: 40px;
}

.modal_input-icon-pass-white {
  display: none;
  position: absolute;
  top: 15px;
  right: 40px;
}

.modal_list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  list-style-type: none;
  display: flex;
}

.modal_link {
  color: gray;
  text-decoration: none;
  display: flex;
}

.modal_link:hover {
  color: #19043a;
  text-decoration-line: underline;
}

@media screen and (width <= 767px) {
  .is_hidden {
    display: none;
  }

  .header_wrapper {
    background-color: "#f6f6f6";
    border: 1.5px solid #000;
    border-radius: 0 0 8px 8px;
    justify-content: center;
    align-items: center;
    max-width: 100vw;
    height: 69px;
    display: flex;
    position: relative;
  }

  .home_shopping_list, .button_login {
    display: none;
  }

  .hamburger_open {
    background-color: inherit;
    border: none;
    display: block;
  }

  .hamburger_close {
    background-color: inherit;
    border: none;
    display: none;
  }

  .btn_log-out-mobile {
    display: block;
  }

  .modal_mobile {
    z-index: 100;
    background: linear-gradient(#4f2ee8 35%, #fff 100%);
    border: 1px solid #000;
    width: 100vw;
    display: none;
    position: absolute;
    top: 70px;
  }

  .modal_mobile-flex {
    flex-direction: column;
    display: flex;
  }

  .img_css-mobile {
    background-image: url("modal-mobile-books.f864b721.png");
    background-repeat: no-repeat;
    background-size: cover;
    height: 439px;
    margin-top: 60%;
    position: relative;
  }

  .btn_log-in-mobile {
    background-color: #4f2ee8;
    border: none;
    width: 184px;
    margin-top: 10%;
    margin-left: 5%;
  }

  .btn_log-out-mobile {
    background-color: inherit;
    border: none;
    display: none;
    position: absolute;
    bottom: 20px;
    left: 20px;
  }

  .modal_signup {
    width: 90%;
  }

  .stefan_btn {
    display: none !important;
  }

  .stefan_div-mobile {
    width: 118dppx;
    background-color: inherit;
    gap: 10px;
    height: 48px;
    margin-top: 15px;
    margin-left: 20px;
    display: flex;
  }

  .stefan-mobile {
    display: none;
  }

  .stefan_name-mobile {
    color: #fff;
    text-transform: uppercase;
    font-size: larger;
  }

  .home_shopping_list-mobile {
    color: #fff;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin: 50% auto;
    display: flex;
    position: relative;
  }

  .shoping-list_text-mobile {
    color: inherit;
  }

  .shopping_link-mobile {
    color: inherit;
    background-color: inherit;
    text-decoration: none;
    display: none;
  }

  .shopping_link-mobile:hover:after {
    content: "";
    z-index: -1;
    background-color: #eac645;
    border-radius: 150px;
    justify-content: center;
    align-items: center;
    width: 180px;
    height: 45px;
    display: flex;
    position: absolute;
    bottom: -13px;
    left: -25px;
  }

  .home_link-mobile {
    background-color: inherit;
    color: inherit;
    text-decoration: none;
    display: none;
  }

  .home_text-mobile {
    color: inherit;
  }

  .home_link-mobile:hover:after {
    content: "";
    z-index: -1;
    background-color: #eac645;
    border-radius: 150px;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 45px;
    display: flex;
    position: absolute;
    top: -13px;
    left: 20px;
  }

  .cart-mobile {
    position: absolute;
    bottom: 0;
    left: 120px;
  }
}

