.loader {
  z-index: 99;
  position: fixed;
  top: 50%;
  left: 50%;
}

.loader__anim {
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: 1s linear infinite rotation;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

.popup-card-container {
  opacity: 1;
  cursor: pointer;
  z-index: 1000;
  background-color: #1116;
  width: 100%;
  height: 100%;
  padding: 94px 15px;
  position: fixed;
  top: 0;
  left: 0;
}

.popup {
  z-index: 30;
  color: var(--black);
  background-color: var(--white);
  border: 2px solid var(--black);
  border-radius: 18px;
  min-width: 300px;
  max-width: 600px;
  padding: 40px 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

.popup-close-button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

button.popup-close-button .popup-iconx path {
  stroke: var(--black);
  fill: var(--black);
}

button.popup-close-button .popup-iconx:hover path {
  stroke: gold;
  fill: gold;
}

.add-to-cart-button {
  background-color: var(--white);
  border: 2px solid var(--violet);
  cursor: pointer;
  color: var(--black);
  letter-spacing: -.14px;
  text-transform: uppercase;
  border-radius: 40px;
  padding: 10px 20px;
